.hljs {
  display: block;
  overflow-x: auto;
  padding: 0.5em;
  background: #fff;
}
.hljs,
.hljs-subst {
  color: #000;
}
.hljs-string,
.hljs-meta,
.hljs-symbol,
.hljs-template-tag,
.hljs-template-variable,
.hljs-addition {
  color: #756bb1;
}
.hljs-comment,
.hljs-quote {
  color: #636363;
}
.hljs-number,
.hljs-regexp,
.hljs-literal,
.hljs-bullet,
.hljs-link {
  color: #31a354;
}
.hljs-deletion,
.hljs-variable {
  color: #88f;
}
.hljs-keyword,
.hljs-selector-tag,
.hljs-title,
.hljs-section,
.hljs-built_in,
.hljs-doctag,
.hljs-type,
.hljs-tag,
.hljs-name,
.hljs-selector-id,
.hljs-selector-class,
.hljs-strong {
  color: #3182bd;
}
.hljs-emphasis {
  font-style: italic;
}
.hljs-attribute {
  color: #e6550d;
}
body {
  margin: 0;
  padding: 0;
  font-family: Microsoft YaHei;
}
.router-link-active {
  color: #41a259 !important;
  background-color: #eeffde !important;
  border-right: 3px solid #41a259 !important;
}
#app {
  overflow: hidden;
  padding: 0;
  width: 100%;
}
#app .main-header {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  -webkit-box-shadow: 0 2px 8px #f0f1f2;
  box-shadow: 0 2px 8px #f0f1f2;
  position: relative;
  z-index: 10;
  height: 65px;
  color: #444;
}
#app .main-header .main-header-logo {
  height: 100%;
  float: left;
  margin-left: 40px;
  position: absolute;
}
#app .main-header .main-header-logo img {
  height: 30px;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}
#app .main-header .main-header-title {
  height: 65px;
  line-height: 65px;
  font-size: 25px;
  display: inline-block;
  padding-left: 75px;
  font-weight: bold;
}
#app .main-header .main-header-nav {
  margin: 0;
  padding: 0;
  float: right;
  height: 100%;
  background: transparent;
  margin-right: 160px;
}
#app .main-header .main-header-nav li {
  list-style: none;
  color: #314659;
  height: 100%;
  line-height: 65px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  min-width: 72px;
  border-top: 2px solid transparent;
  text-align: center;
  cursor: pointer;
  font-size: 15px;
  float: left;
  border-bottom: 3px solid #fff;
  -webkit-transition: color 0.3s cubic-bezier(0.645, 0.045, 0.355, 1),
    border-color 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: color 0.3s cubic-bezier(0.645, 0.045, 0.355, 1),
    border-color 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
}
#app .main-header .main-header-nav li.active {
  color: #41a259;
  border-bottom: 3px solid #41a259;
}
#app .main- {
  position: relative;
  padding: 40px 0 0;
  margin-left: 0;
  margin-right: 0;
  height: auto;
  zoom: 1;
  display: block;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
#app .main- .main--left {
  float: left;
  -webkit-box-flex: 0;
  -ms-flex: 0 0 auto;
  flex: 0 0 auto;
}
#app .main- .main--left ul {
  width: 250px;
  margin: 0;
  padding: 0;
}
#app .main- .main--left ul li {
  margin: 0;
  list-style: none;
  padding: 0;
  font-size: 14px;
  overflow: hidden;
  text-overflow: ellipsis;
  cursor: pointer;
}
#app .main- .main--left ul li a {
  font-size: 14px;
  color: #444;
  line-height: 40px;
  height: 40px;
  margin: 0;
  padding: 4px 25px;
  text-decoration: none;
  white-space: nowrap;
  display: block;
  position: relative;
  -webkit-transition: 0.15s ease-out;
  transition: 0.15s ease-out;
  font-weight: 700;
  border-right: 1px solid #e8e8e8;
  font-weight: 400;
}
#app .main- .main--left ul li a:hover {
  color: #41a259;
}
#app .main- .main--left ul li.main--link-group {
  height: auto;
}
#app .main- .main--left ul li.main--link-group a:hover {
  color: #333;
  cursor: default;
}
#app .main- .main--left ul li.main--link-group ul a {
  display: block;
  height: 40px;
  color: #444;
  line-height: 40px;
  font-size: 14px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-weight: 400;
}
#app .main- .main--left ul li.main--link-group ul a:hover {
  color: #41a259;
  cursor: pointer;
}
#app .main- .main--right {
  padding: 0 84px 140px 64px;
  min-height: 500px;
  overflow: hidden;
}
#app .main- .main--right a {
  color: #41a259;
  text-decoration: none;
}
.markdown h1 {
  color: #0d1a26;
  font-weight: 500;
  margin-bottom: 20px;
  margin-top: 8px;
  font-size: 30px;
  line-height: 38px;
}
.markdown h3 {
  font-size: 18px;
}
.markdown h4 {
  font-size: 16px;
}
.markdown h5 {
  font-size: 14px;
}
.markdown h6 {
  font-size: 12px;
}
.markdown p,
.markdown pre {
  font-size: 14px;
  color: #5e6d82;
  line-height: 1.5em;
}
.markdown ul > li > p,
.markdown ol > li > p {
  margin: 0.2em 0;
}
.markdown ul {
  padding: 0;
  margin: 0;
}
.markdown ul > li {
  list-style-type: circle;
  margin-left: 20px;
  padding-left: 4px;
  padding-top: 15px;
}
.markdown ol > li {
  list-style-type: decimal;
  margin-left: 20px;
  padding-left: 4px;
  padding-top: 15px;
}
.markdown > table {
  border-collapse: collapse;
  border-spacing: 0;
  empty-cells: show;
  border: 1px solid #ebedf0;
  width: 100%;
  margin: 8px 0 16px;
}
.markdown > table th,
.markdown > table td {
  color: #314659;
  border: 1px solid #ebedf0;
  text-align: left;
  padding: 10px 15px;
}
.markdown > table th {
  white-space: nowrap;
  color: #5c6b77;
  font-weight: 500;
  background: rgba(0, 0, 0, 0.02);
}
.markdown strong,
.markdown b {
  font-weight: 500;
}
.markdown .sh-markdown-permalink {
  opacity: 0;
}
.markdown h1:hover .sh-markdown-permalink,
.markdown h2:hover .sh-markdown-permalink,
.markdown h3:hover .sh-markdown-permalink,
.markdown h4:hover .sh-markdown-permalink,
.markdown h5:hover .sh-markdown-permalink,
.markdown h6:hover .sh-markdown-permalink {
  opacity: 1;
  display: inline-block;
}
.markdown > br,
.markdown p > br {
  clear: both;
}
.markdown blockquote {
  font-size: 90%;
  color: #0d1a26;
  border-left: 4px solid #ebedf0;
  padding-left: 0.8em;
  margin: 1em 0;
}
.markdown blockquote p {
  margin: 0;
}
.markdown hr {
  height: 1px;
  border: 0;
  background: #ebedf0;
  clear: both;
}
.markdown code {
  font-family: Microsoft YaHei;
  margin: 0 1px;
  background: #f2f4f5;
  padding: 0.2em 0.5em;
  border-radius: 3px;
  font-size: 0.9em;
  border: 1px solid #eee;
}
