.weui-toast {
  position: fixed;
  z-index: 5001;
  width: 7.6em;
  min-height: 7.6em;
  top: 180px;
  left: 50%;
  margin-left: -3.8em;
  background: rgba(17, 17, 17, 0.7);
  text-align: center;
  border-radius: 5px;
  color: #FFFFFF;
}
.weui-icon_toast {
  margin: 22px 0 0;
  display: block;
}
.weui-icon_toast.weui-icon-success-no-circle:before {
  color: #FFFFFF;
  font-size: 55px;
}
.weui-icon_toast.weui-loading {
  margin: 30px 0 0;
  width: 38px;
  height: 38px;
  vertical-align: baseline;
}
.weui-toast__content {
  margin: 0 0 15px;
}
.weui-loading {
  width: 20px;
  height: 20px;
  display: inline-block;
  vertical-align: middle;
  -webkit-animation: weuiLoading 1s steps(12, end) infinite;
          animation: weuiLoading 1s steps(12, end) infinite;
  background: transparent url("data:image/svg+xml;base64,") no-repeat;
  background-size: 100%;
}
.weui-loading.weui-loading_transparent {
  background-image: url("data:image/svg+xml;base64,");
}
@-webkit-keyframes weuiLoading {
0% {
    -webkit-transform: rotate3d(0, 0, 1, 0deg);
            transform: rotate3d(0, 0, 1, 0deg);
}
100% {
    -webkit-transform: rotate3d(0, 0, 1, 360deg);
            transform: rotate3d(0, 0, 1, 360deg);
}
}
@keyframes weuiLoading {
0% {
    -webkit-transform: rotate3d(0, 0, 1, 0deg);
            transform: rotate3d(0, 0, 1, 0deg);
}
100% {
    -webkit-transform: rotate3d(0, 0, 1, 360deg);
            transform: rotate3d(0, 0, 1, 360deg);
}
}
.vux-loading .weui-toast {
  z-index: 5001;
}
.weui-icon_toast.weui-loading {
  margin: 30px 0 0;
  width: 38px;
  height: 38px;
  vertical-align: baseline;
  display: inline-block;
}
.vux-mask-enter,
.vux-mask-leave-active,
.vux-mask-leave-active,
.vux-mask-enter-active {
  position: relative;
  z-index: 1;
}
.vux-mask-enter,
.vux-mask-leave-active {
  opacity: 0;
}
.vux-mask-leave-active,
.vux-mask-enter-active {
  -webkit-transition: opacity 300ms;
  transition: opacity 300ms;
}
.vux-loading-no-text .weui-toast {
  min-height: 98px;
}

.vux-spinner {
  stroke: #444;
  fill: #444;
  vertical-align: middle;
  display: inline-block;
  width: 28px;
  height: 28px;
}
.vux-spinner svg {
  width: 28px;
  height: 28px;
}
.vux-spinner.vux-spinner-inverse {
  stroke: #fff;
  fill: #fff;
}
.vux-spinner-android {
  stroke: #4b8bf4;
}
.vux-spinner-ios, .vux-spinner-ios-small {
  stroke: #69717d;
}
.vux-spinner-spiral .stop1 {
  stop-color: #fff;
  stop-opacity: 0;
}
.vux-spinner-spiral.vux-spinner-inverse .stop1 {
  stop-color: #000;
}
.vux-spinner-spiral.vux-spinner-inverse .stop2 {
  stop-color: #fff;
}

.kt-button {
  border: 1px solid #41a259;
  background-color: #41a259;
  display: inline-block;
  border-radius: 2px;
  height: 14px;
  line-height: 14px;
  color: #fff;
  padding: 10px 19px;
  cursor: pointer;
  white-space: nowrap;
}

